  result = 'Use ' + window.location.host + window.location.pathname + '?q=Your question here'
  updateResult() {
    if (this.query === undefined) return
    try {
      let isOk = false
      this.data.forEach((pair) => {
        if (pair[0].includes(this.query)) {
          this.result = pair[1];
          isOk = true;
          return;
        }
      })
      if (!isOk) this.result = "404: Not found";
    }
    catch (err) {
      this.result = "500: Internal error";
    }
  }
  get query() {
    return this.$route.query.q as string;
  }
  mounted() {
    this.updateResult();
  }

  @Watch('query')
  onQueryChange() {
    this.updateResult();
  }
}
